.form-container {
  position: absolute;
  transition: all 0.6s ease-in-out;
  top: 0;
  height: 100%;
  left: 0;
}

.sign-up-container {
  z-index: 2;
  width: 50%;
}

.sign-in-container {
  z-index: 1;
  opacity: 0;
  width: 50%;
  left: 0;
}

.overlay-container {
  position: absolute;
  left: 50%;
  width: 50%;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  z-index: 100;
  height: 100%;
  overflow: hidden;
}

.overlay {
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  background: #ff416c;
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
}

.overlay-panel {
  position: absolute;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
